body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.document-container {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-content: center;
          align-content: center;
  text-align: center;
}

.document-container table {
  border-collapse: collapse;
  border: 2px solid black;
  width: 50%;
  margin: 30px auto;
}

.document-container table td, .document-container table th {
  text-align: center;
  padding: 5px;
}

.medical-process-container .set-done,
.medical-process-container .edit {
  border-radius: 50%;
  width: 25px;
  height: 25px;
  background: #646464;
  margin: 0 0 0 10px;
  padding: 0;
  color: white;
  font-size: 14px;
}

.medical-process-container .content {
  overflow-y: auto;
  height: 50vh;
}

.medical-process-container form {
  display: -webkit-flex;
  display: flex;
  width: 100%;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  -webkit-align-items: center;
          align-items: center;
  height: 70px;
}

.medical-process-container form input,
.medical-process-container form select {
  margin: 20px 5px 10px 5px;
  -webkit-flex: 2 1 250px;
          flex: 2 1 250px;
}

.medical-process-container form textarea {
  margin: 30px 10px 0 10px;
  height: 50px;
  -webkit-flex: 2 1 300px;
          flex: 2 1 300px;
}

.medical-process-container ul {
  list-style: none;
  padding-left: 0;
}

.medical-process-container ul li {
  margin-bottom: 30px;
}

.medical-process-container ul li input {
  margin: 0;
}

.medical-process-container ul li #title {
  width: 300px;
}

.medical-process-container ul li #details {
  width: 500px;
}

body {
  font-size: 0.9em;
}

.header {
  background: #929292;
  height: 90px;
  width: 100%;
  position: relative;
}

.header .radio-buttons {
  padding: 25px;
}

.header .log-out {
  position: absolute;
  top: 12px;
  right: 10px;
  margin: 15px;
}

ul {
  list-style: none;
}

a {
  color: black;
  text-decoration: none;
}

input,
select {
  margin: 20px;
  padding: 5px;
}

.container {
  background: #e4dcc4;
  width: 60%;
  height: 65vh;
  margin: 15px 6%;
  border-radius: 0 10px 10px 10px;
  padding: 20px 50px;
  box-shadow: 5px 5px 5px #585858;
  position: relative;
}

button,
input[type="submit"] {
  padding: 10px 20px;
  border: 0;
  border-radius: 5px 5px;
  background: #e4ab6a;
  color: black;
  font-size: 0.9em;
  width: 200px;
  margin: 20px auto;
  cursor: pointer;
  transition: all 0.3s ease-in-out;
}

button:hover,
input[type="submit"]:hover {
  background: #eb9a4e;
}

.login-container {
  margin: 70px auto;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-content: center;
          align-content: center;
  text-align: center;
  height: 75vh;
  border-radius: 10px;
}

.login-container #login-info {
  margin: 5px 0 0 0;
}

.login-container form {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  width: 30%;
  margin: 50px auto;
  margin-bottom: 10px;
}

.login-container button {
  margin-top: 0;
}

.register-container {
  margin: 15px auto;
  height: 85vh;
  border-radius: 10px;
}

.register-container p,
.register-container h2 {
  text-align: center;
}

.register-container form {
  display: grid;
  width: 50%;
  margin: auto;
}

.register-container form span {
  padding: 7px;
  height: 40px;
}

.register-container form input,
.register-container form select {
  float: right;
  width: 200px;
  margin-top: 0;
}

.register-container form select {
  width: 210px;
}

.content {
  overflow-y: auto;
}

.backButton {
  position: absolute;
  top: 2%;
  left: 30px;
}

.documentation-container button {
  margin: 30px 40px;
}

.documentation-container .buttons {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
}

.documentation-container .documents {
  height: 41vh;
  text-align: left;
  overflow-y: auto;
}

.documentation-container .filterForm {
  margin: 20px 0 0 20px;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: flex-start;
          justify-content: flex-start;
  width: 100%;
  -webkit-flex-wrap: nowrap;
          flex-wrap: nowrap;
}

.documentation-container .filterForm input, .documentation-container .filterForm select {
  margin: 0 10px;
  width: 120px;
}

.documentation-container ul {
  margin-top: 40px;
  padding-left: 0;
}

.documentation-container ul li {
  margin: 20px;
}

.recommendations-container {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
}

.recommendations-container button {
  margin-bottom: 10px;
}

.recommendations-container a {
  margin: 10px auto;
  width: 200px;
}

.recommendation {
  padding: 10px 30px;
  background: #838383;
  text-align: left;
  cursor: pointer;
  color: white;
  box-shadow: 2px 2px 2px #333;
  width: 60%;
  margin: 5px auto 20px auto;
}

.side-bar {
  position: absolute;
  top: 90px;
  right: 0;
  width: 18%;
  height: 100vh;
  background: #c9c9c9;
  padding: 15px 20px;
}

.side-bar h3 {
  margin: 10px 0 10px 0;
}

.side-bar button {
  display: block;
}

.side-bar ul {
  list-style: none;
  padding-left: 0;
}

.side-bar ul input {
  margin: 10px;
}

.side-bar form {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
}

.side-bar form input {
  margin: 10px auto;
  width: 200px;
}

.side-bar table {
  width: 100%;
}

.side-bar table td {
  width: 50%;
  padding: 4px;
}

.side-bar table td p {
  margin: 0;
}

.side-bar table td input {
  margin: 0;
  width: 100px;
}

nav {
  margin: 40px 40px 0px 6%;
}

nav a {
  border: 1px solid #969696;
  background: #aaaaaa;
  padding: 20px;
  text-decoration: none;
  border-radius: 10px 10px 0 0;
  color: black;
}

nav a.active {
  background: #e4dcc4;
  border: 1px solid #e4dcc4;
}

.ID-form {
  position: absolute;
  top: 10px;
  left: 30%;
}

.raport-content {
  width: 90%;
  margin: auto;
  text-align: center;
}

.raport-content table {
  border-collapse: collapse;
  border: 2px solid black;
}

.raport-content table td {
  padding: 10px 7px;
  width: 250px;
  text-align: center;
}

.new-document {
  width: 60%;
  margin: 20px auto;
}

.new-document input {
  width: 300px;
  margin: 0 20px 10px 30px;
  float: right;
}

.new-document input[type="submit"] {
  width: 300px;
  margin: 0 auto;
}

.new-document select {
  margin: 0 20px 10px 30px;
  margin-left: 110px;
  width: 310px;
}

.new-document textarea {
  margin: 0 20px 20px 0;
  width: 310px;
  height: 100px;
  float: right;
}

.new-document .document-form {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
}

.form-container {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
}

.form-container .attachment-button {
  width: 150px;
  margin: 10px;
}

.form-container .attachments-info {
  margin-bottom: 0;
}

.form-container .attachments-info a {
  text-decoration: underline;
