  height: 70px;
}

.form-container form input {
  margin-bottom: 10px;
}

.attachment-form {
  width: 60%;
  margin: 40px auto;
  display: grid;
  -webkit-justify-content: center;
          justify-content: center;
}

.attachment-form input[type="text"],
.attachment-form input[type="date"] {
