  -webkit-justify-content: center;
          justify-content: center;
}

h2 {
  text-align: center;
}

.lab-container {
  text-align: center;
  width: 70%;
  margin: 10px auto !important;
  height: 75vh;
  overflow: auto;
}

.lab-container form {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: flex-end;
          align-items: flex-end;
  width: 60%;
