.table {
  width: 90%;
  margin: 0 auto;
}

.table table {
  width: 70%;
  text-align: left;
  margin: auto;
  border-collapse: collapse;
  border: 2px solid black;
}

.table table td, .table table th {
  margin: 10px;
  padding: 5px;
  text-align: center;
}

